A,WAAA,CAEA,WAAA,CAHA,UAhBlB,CACF,CAyBA,gCACI,eAvBJ,CA0BA,uCAEI,kBAAA,CACA,cAAA,CAFA,YArBJ,CA0BA,0CACI,gBAvBJ,CA0BA,sCAEI,aAAA,CADA,cAtBJ,CA0BA,0CAGI,cAAA,CADA,WAAA,CADA,UArBJ","sourcesContent":["\n.page-profile {\n    &__title {\n        font-size: 32px;\n        font-weight: 700;\n        margin-bottom: 20px;\n        color: var(--color-text);\n        font-family: Oswald !important;\n    }\n\n    .player-info {\n        display: flex;\n        justify-content: space-between;\n        margin-bottom: 20px;\n        gap: 16px;\n\n        &__card {\n            height: 200px;\n            border-radius: 8px;\n            background-color: var(--color-content);\n            display: flex;\n            align-items: center;\n            justify-content: center;\n        }\n\n        &__statistics {\n            display: flex;\n            gap: 20px;\n            background-color: var(--color-content);\n            width: 100%;\n            height: 200px;\n            border-radius: 8px;\n            align-items: center;\n            justify-content: space-around;\n        }\n\n        .statistics-item {\n            display: flex;\n            flex-direction: column;\n            align-items: center;\n\n            &__icon {\n                width: 80px;\n                height: 80px;\n                border-radius: 8px;\n                border: 1px solid var(--color-text);\n                padding: 20px;\n                margin-bottom: 12px;\n\n                img {\n                    width: 100%;\n                    height: 100%;\n                    object-position: center;\n                    object-fit: contain;\n                }\n            }\n\n            &__title {\n                font-size: 14px;\n                font-weight: 300;\n                margin-bottom: 12px;\n                color: var(--color-text-secondary);\n                white-space: nowrap;\n                text-overflow: ellipsis;\n                overflow: hidden;\n            }\n\n            &__value {\n                font-size: 16px;\n                font-weight: 700;\n                color: var(--color-text);\n                margin: 0;\n                white-space: nowrap;\n                text-overflow: ellipsis;\n                overflow: hidden;\n            }\n        }\n\n        .card {\n            display: flex;\n            flex-direction: column;\n            align-items: center;\n            width: 320px;\n            flex-shrink: 0;\n\n            &__img {\n                width: 80px;\n                height: 80px;\n                object-position: center;\n                object-fit: cover;\n                border-radius: 8px;\n            }\n\n            &__name {\n                font-size: 16px;\n                font-weight: 700;\n                margin-top: 12px;\n                color: var(--color-text);\n            }\n\n            &__desc {\n                font-size: 14px;\n                font-weight: 300;\n                text-align: center;\n                color: var(--color-text-secondary);\n                margin-top: 8px;\n            }\n        }\n    }\n}\n\n@media (max-width: 1024px) {\n    .page-profile {\n        &__title {\n            display: none;\n        }\n\n        .player-info {\n            flex-direction: column;\n            gap: 16px;\n\n            &__statistics {\n                display: grid;\n                grid-template-columns: (repeat(2, 1fr));\n                height: auto;\n                padding: 15px;\n            }\n\n            .card {\n                width: 100%;\n            }\n\n            .statistics-item {\n                &__icon {\n                    width: 42px;\n                    height: 42px;\n                    border: none;\n                    padding: 7px;\n                }\n            }\n        }\n    }\n}\n\n.auto-withdraw {\n    margin-top: 15px;\n}\n\n.auto-withdraw__label {\n    display: flex;\n    align-items: center;\n    cursor: pointer;\n}\n\n.auto-withdraw__checkbox {\n    margin-right: 8px;\n}\n\n.auto-withdraw__text {\n    font-size: 14px;\n    color: #e9e5e5;\n}\n\n.auto-withdraw__checkbox {\n    width: 18px;\n    height: 18px;\n    cursor: pointer;\n}\n"],"sourceRoot":""}]);
